<template>
	<footer v-if='showShow'>
		  <div class="left">
		  	    <div class="bar">
		  	    	<img src="../../static/img/tt.png"/>
		  	    </div>
		  	    <div class="bar1">
		  	    	 <p>今日头条</p>
		  	    	 <p>你关心的，才是头条</p>
		  	    </div>
		  </div>
		  
		  
		   <div class="right">
		   	<div >立即打开</div>
		  	    <div class="logo"@click="showShow=!showShow">
		  	    	<img src="../../static/img/xx.png"/>
		  	    </div>
		  	    
		  </div>
	</footer>
	
</template>

<script>
	export default {
 
  data () {
    return {
       showShow:true
    }
  }
 }
</script>

<style>
	 
	footer{
		width: 100%;
		height: 2.2rem;
		position: fixed;
		bottom: 0;
		background: #4A4D4A;
		display: flex;
		justify-content: space-between;
		 align-items: center;
		
	}
	footer >.left{
		height: 2.2rem;
		padding-left: 0.3rem;
		display: flex;
		align-items: center;
		
		
	}
	
	footer >.left .bar{
		   width: 0.8rem;
		     height: 0.8rem;
	}
	
	footer >.left .bar img{
		   width: 100%;
		     height: 100%;
	}
		footer >.left .bar1 p:first-child{
		       font-size: 1rem;
		       color: white;
	}
	
	footer >.left .bar1 p:last-child{
		       font-size: 0.7rem;
		       color: white;
	}
	footer >.right{
		height: 2.2rem;
		padding-left: 0.3rem;
		padding-right: 0.2rem;
		display: flex;
		align-items: center;
		
	}
	footer >.right div:first-child{
		width: 3rem;
		height: 1.5rem;
		background: #FF444C;
		text-align: center;
		line-height: 1.5rem;
		color: white;
		font-size: 0.3rem;
		border-radius: 5px;
		
	} 
	footer >.right .logo{
	    
	     padding-right: 0.3rem;
	     padding-left: 0.45rem;
	     } 
	     footer >.right .logo img{
	     width: 100%;
	     height: 100%;
	     } 
	
</style>


